export default class Services {

    getProfileFetch = () => {
        const token = localStorage.getItem("acessToken");
        
        return  fetch('http://react-test.somee.com/api/user', {
            method: "GET",
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
              'Authorization': `${token}`
            }
          })
           
        
    }
}